/* Header.css */

#navigation-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 30px;
}

.App-logo {
  width: 200px;
  height: auto;
}

#nav-wrap {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  background: transparent; /* ✅ Add or ensure this */
  box-shadow: none;         /* ✅ Remove any shadows */
  padding: 1rem 2rem;
  border-radius: 12px;
}


#main-nav {
  display: flex;
  justify-content: center;
}

.nav-list {
  list-style: none;
  display: flex;
  gap: 48px;
  padding: 0;
  margin: 0;
}

.nav-list li {
  position: relative;
  font-weight: 600; /* less intense than bold */
  font-size: 1.15rem;
  letter-spacing: 0.5px; /* adds breathing room between letters */
  text-transform: uppercase; /* optional: gives uniform look */
  color: white;
  transition: color 0.3s ease;
}

.nav-list li:hover {
  color: #ccc; /* subtle hover color */
}

.dropdown-toggle {
  cursor: pointer;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
}

.dropdown-toggle::after {
  content: '';
  display: block;
  width: 0%;
  height: 2px;
  background: white;
  transition: width 0.3s;
}

.dropdown-toggle:hover::after {
  width: 100%;
}

.dropdown-menu {
  display: none;
  position: absolute;
  top: 100%;
  padding: 10px 0;
  border-radius: 6px;
  z-index: 1000;
  min-width: 160px;
  color: white;
  background-color: #91c8b0;
  
}

.dropdown-menu li {
  padding: 8px 20px;
  white-space: nowrap;
  
}

.dropdown-menu li:hover {
  padding: 8px 20px;
  white-space: nowrap;
  background-color: #91c8b0; /* ✅ Your desired background on hover */

}

.dropdown:hover .dropdown-menu {
  display: block;
  
}

.dropdown-menu a {
  text-decoration: none;
  color: #000; /* Normal text color */
  display: block;
  padding: 8px 20px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.dropdown-menu a:hover {
  background-color: red; /* ✅ Your desired background on hover */
  color: #fff; /* ✅ Optional: change text color on hover */
}

.dropdown-toggle:hover {
  color: #ccc;
  
}

#logo-text {
  color: white;
  font-weight: 100;
  font-size: 50px;
  margin-right: 20px;
}