/* ========== Fonts ========== */
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 300;
  src: url("./assets/fonts/poppins/poppins-v23-latin-300.woff2") format("woff2");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  src: url("./assets/fonts/poppins/poppins-v23-latin-regular.woff2") format("woff2");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  src: url("./assets/fonts/poppins/poppins-v23-latin-500.woff2") format("woff2");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  src: url("./assets/fonts/poppins/poppins-v23-latin-600.woff2") format("woff2");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  src: url("./assets/fonts/poppins/poppins-v23-latin-700.woff2") format("woff2");
}

/* ========== Global Layout ========== */
html, body {
  font-family: 'Poppins', sans-serif;
  background-image: url('bt.jpeg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  margin: 0;
  padding: 0;
  background-color: #0a0a0a;
}

.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin-top: 64px;
}

.App {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.content {
  flex: 1;
}

/* ========== Navigation ========== */
.nav-logo {
  margin-top: 1rem;
  margin-bottom: 0.5rem;
}

#nav-wrap {
  margin-top: 10px;
}

.nav-list {
  display: flex;
  list-style: none;
  gap: 4rem;
  justify-content: center;
  padding: 0;
  margin: 0;
  font-size: 20px;
  position: relative;
}

.nav-list li {
  position: relative;
}

.dropdown-toggle {
  cursor: pointer;
  font-weight: 600;
  color: white;
  font-size: 24px;
}

/* ========== Dropdown Menu ========== */
.dropdown-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #ffffff;
  padding: 0.5rem 0;
  min-width: 180px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  z-index: 99;
  border-radius: 6px;
  list-style: none;
  margin: 0;
  font-size: 14px;
  font-weight: 500;
}

.dropdown-menu li a {
  display: block;
  padding: 0.5rem 1rem;
  color: #2C2D31;
  text-decoration: none;
}

.dropdown-menu li a:hover {
  color: #50796A;
  background-color: #F0F7F4;
}

.dropdown:hover .dropdown-menu {
  display: block;
}

/* ========== Footer ========== */
#footer-div {
  background-color: #1a1a1a;
  color: #f0f0f0;
  padding: 2rem 1rem;
  border-radius: 12px;
}

#footer-div, #footer-div a, #footer-div p, #footer-div h4, #footer-div h5, #footer-div span, #footer-div .footer-link {
  color: #f0f0f0;
  background-color: #1a1a1a !important;
}

#Footer-links {
  display: flex;
  justify-content: center;
  gap: 3rem;
  margin-top: 1rem;
}

#Footer-links div h4 {
  margin-bottom: 0.5rem;
  font-size: 16px;
  color: #ffffff;
}

#Footer-links .footer-link {
  text-decoration: none;
  color: #cccccc;
  display: block;
  margin-bottom: 0.3rem;
  font-size: 14px;
  transition: color 0.3s ease;
}

#Footer-links .footer-link:hover {
  color: #91c8b0;
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-top: 1rem;
}

.social-icons a svg {
  color: #f0f0f0;
  fill: #f0f0f0;
}

.social-icons a:hover {
  color: #91c8b0;
}

.social-icon {
  width: 32px;
  height: 34px;
  filter: brightness(0);
  transition: filter 0.3s ease;
  margin: 0 10px;
}

.social-icon:hover {
  filter: brightness(0) saturate(100%) sepia(20%) hue-rotate(100deg);
}

/* ========== Main Layout ========== */
#Main-body {
  padding: 2rem;
}

.background-overlay {
  background-color: rgba(0, 0, 0, 0.55);
  padding: 20px 40px;
  border-radius: 16px;
  max-width: 1300px;
  margin: 0 auto;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.5);
}

.main-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto 1rem auto;
}

.main-footer-right {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: flex-end;
  font-size: 12px;
  color: #2c2d31;
}

.main-footer-right a {
  color: whitesmoke;
  text-decoration: none;
  font-weight: 500;
  margin-left: 0.75rem;
  background-color: rgba(44, 45, 49, 0.85);
  border-radius: 12px;
  padding: 1rem 2rem;
}

.main-footer-right a:hover {
  text-decoration: underline;
}

.App-logo {
  height: 140px;
  max-width: 100%;
  object-fit: contain;
}

.App-header {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#news-banner {
  width: 100%;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(6px);
  display: flex;
  justify-content: center;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.4);
  padding: 0.75rem 0;
  border-radius: 0 0 12px 12px;
  margin-bottom: 2rem;
  max-width: 960px;
  align-items: center;
  color: white;
  font-size: 24px;
  background-color: #2c2d31;
  height: 50px;
}

#news-banner-wrapper {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  height: 70px;
}

/* ========== Responsive ========== */
@media (max-width: 768px) {
  .main-content {
    flex-direction: column;
    align-items: center;
  }

  .main-footer-right {
    justify-content: center;
    text-align: center;
    font-weight: bold;
  }

  #navigation-bar {
    flex-direction: column;
    align-items: center;
    background-color: rgba(44, 45, 49, 0.65);
    backdrop-filter: blur(6px);
    -webkit-backdrop-filter: blur(6px);
    border-radius: 12px;
    padding: 1rem 2rem;
  }

  #nav-wrap {
    margin-top: 0.5rem;
    width: 100%;
  }

  .nav-list {
    flex-direction: column;
    gap: 1rem;
    padding: 0;
  }

  .nav-logo {
    margin-bottom: 1rem;
  }

  #main-nav {
    width: 100%;
  }
}

.nav-link:hover {
  color: #00ffee;
  transition: color 0.3s ease;
}
